header {
  min-height: 65vh;
  width: 95%;
  margin-top: 2.5%;
  margin: 0 auto;
  background-image: url("../../images/meetup-image-min.jpg");
  background-position: center;
  background-size: cover;
}

header .header-content {
  margin-top: 2.5%;
  min-height: 65vh;
  width: 100%;
  background: rgba(53, 53, 53, 0.685);
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

header .header-content h1 {
  font-size: 70px;
  font-weight: bolder;
  text-align: center;
  font-family: "Roboto", sans-serif;
  letter-spacing: 1px;
}

header .header-content h2 {
  font-size: 22px;
  text-align: center;
  font-weight: normal;
  max-width: 500px;
  line-height: 30px;
  padding-top: 0.5rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

header .header-content .button-wrapper {
  padding-top: 2.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  max-width: 410px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
}

@media (max-width: 600px) {
  header .header-content .button-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }
}

header .header-content h3 {
  color: white;
  text-align: center;
  padding-top: 1.5rem;
}

header .header-content .status-container {
  position: fixed;
  bottom: 0;
  left: 0;
  background: mediumaquamarine;
  color: white;
  padding: 1rem;
  width: 100%;
  z-index: 100;
}

header .header-content .status-container h3 {
  font-size: 0.9em;
  padding: 0;
}

header .header-content .status-container-err {
  position: fixed;
  bottom: 0;
  left: 0;
  background: orangered;
  color: white;
  width: 100%;
  padding: 1rem;
  z-index: 100;
}

header .header-content .status-container-err h3 {
  font-size: 0.9em;
  padding: 0;
}


